import Vue from 'vue'
import VueRouter from 'vue-router'
import Rankings from '../views/Rankings.vue'
import Search from '../views/Search.vue'
import Show from '../views/Show.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Rankings',
    component: Rankings
  },
  {
    path: '/search',
    name: 'Search',
    component: Search
  },
  {
    path: '/show',
    name: 'Show',
    component: Show
  }
]

const router = new VueRouter({
  routes
})

export default router
